.container {
    display: flex;
    height: 100vh;
    //align-items: center;
    // justify-content: flex-start;
    // padding: 8px;
    // border-bottom: 1px solid #000;
    // font-family: Arial, sans-serif;
}
.navigation {
    flex-direction: column;
    flex-shrink: 0;
    width: 120px;
    color: black;
    font-family: Arial, sans-serif;
    border-right: 2px solid #000;
    padding: 4px 8px; /* Adjust padding as needed */
}
 
.navigation div {
    display: block;
    text-decoration: none;
    color: black; /* Or any color you prefer */
    font-family: Arial, sans-serif;
    padding: 8px 4px; /* Adjust padding as needed */
}
  
.navigation a {
    display: block;
    text-decoration: none;
    color: blue; /* Or any color you prefer */
    font-family: Arial, sans-serif;
    padding: 8px 4px; /* Adjust padding as needed */
}

iframe {
    display: flex;
    width: 90%; 
    height: 600px;
}

img {
    display: flex;
    width: 160px;
    height: 160px;
}

p {
    font-family: Arial, sans-serif;
    font-size: 16px;
}

li {
    font-family: Arial, sans-serif;
    font-size: 16px;
}

.current {
    color: green;
}
.base {
    color: blue;
}

.content {
    flex-grow: 1;
    padding-left: 50px; /* This adds space between the nav bar and the content area */
    padding-right: 100px;
}

.app-container {
    display: flex;
}

.form-container {
    width: 50%;
}

.result-container {
    width: 40%;
}

